import { Component } from 'react';
import ErrorComponent from './ErrorComponent';



export default class ErrorBoundary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
    };
  }

  componentDidCatch(error) {
    this.setState({ error });
  }

  render() {
    if (this.state.error) {
      return (<ErrorComponent />);
    }

    return (this.props.children);
  }
}
