import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';
import LinkExternal from '../components/Link/LinkExternal';

import Team1 from "../assets/images/team/team1.png"; // 120x120
import Team1X2 from "../assets/images/team/team1@2x.png"; // 240x240
/*import Team2 from "../assets/images/team/team2.png";
import Team2X2 from "../assets/images/team/team2@2x.png";
import Team3 from "../assets/images/team/team3.png";
import Team3X2 from "../assets/images/team/team3@2x.png";
import Team4 from "../assets/images/team/team4.png";
import Team4X2 from "../assets/images/team/team4@2x.png";*/

//import TelegramIcon from "../assets/images/soc/telegramm.svg";
import TwitterIcon from "../assets/images/soc/twitter.svg";
import FacebookIcon from "../assets/images/soc/facebook.svg";
import InstagramIcon from "../assets/images/soc/instagram.svg";
import LinkedInIcon from "../assets/images/soc/linkedin.svg";

import './ScreenTeam.scss';



const swiperOptions = {
  breakpoints: {
    320: { slidesPerView: 'auto', spaceBetween: 12, },
    991: { slidesPerView: 4, spaceBetween: 32, },
  }
};

const team = [
  {
    fullName: 'Aleksandr Ivannikov', position: 'Founder & CEO',
    image: Team1, imageX2: Team1X2,
    contacts: {
      twitter: 'https://twitter.com/an_ivannikov',
      facebook: 'https://www.facebook.com/an.ivannikoff/',
      instagram: 'https://www.instagram.com/an_ivannikov/',
      linkedin: 'https://www.linkedin.com/in/an-ivannikov/',
    },
  },
];

export default function ScreenTeam() {
  return (
    <section className="section-team" data-aos="flip-up">
      <div className="container">
        <div className="row">
          <div className="col-12 col-xl-8">
            <h2 className="title">Our Team</h2>
            <p className="desc"></p>
          </div>
        </div>

        <Swiper
          className="teams"
          slidesPerView={1}//"4"
          spaceBetween="32"
          breakpoints={swiperOptions.breakpoints}
        >
          {team.map((item, index) => {
            return (<SwiperSlide key={index}>
              <div className="team" data-aos="fade-down-right" key={index}>
                <div className="team-item">
                  <span className="team-item__img">
                    <img
                      width="120" height="120"
                      src={item.image} srcSet={`${item.imageX2} 2x`}
                      alt="" title=""
                    />
                  </span>
                  <div className="team-item__desc">{item.position}</div>
                  <div className="team-item__title">{item.fullName}</div>
                  {item.contacts && <div className="team-item__soc soc">
                    {item.contacts.twitter && <LinkExternal href={item.contacts.twitter}>
                      <img src={TwitterIcon} alt="" title="24" width="24" height="" />
                    </LinkExternal>}
                    {item.contacts.facebook && <LinkExternal href={item.contacts.facebook}>
                      <img src={FacebookIcon} alt="" title="24" width="24" height="" />
                    </LinkExternal>}
                    {item.contacts.instagram && <LinkExternal href={item.contacts.instagram}>
                      <img src={InstagramIcon} alt="" title="24" width="24" height="" />
                    </LinkExternal>}
                    {item.contacts.linkedin && <LinkExternal href={item.contacts.linkedin}>
                      <img src={LinkedInIcon} alt="" title="24" width="24" height="" />
                    </LinkExternal>}
                  </div>}
                </div>
              </div>
            </SwiperSlide>);
          })}
        </Swiper>
      </div>
    </section>
  );
};
